@font-face {
  font-family: Rainy Hearts;
  src: url("rainyhearts.ca8c35be.ttf") format("truetype");
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Rainy Hearts, sans-serif;
  overflow: hidden;
}

body {
  color: #000;
  background-color: #457237;
  justify-content: center;
  align-items: center;
}

#gameContainer {
  z-index: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}

#chatBox {
  z-index: 100;
  color: #00008b;
  pointer-events: none;
  background: #ffffffb3;
  border-radius: 5px;
  min-width: 300px;
  max-width: 70%;
  margin: 15px;
  padding: 15px;
  inset: auto auto 0 0;

  & #messageInput {
    z-index: 101;
    pointer-events: auto;
    width: max(95%, 200px);
    font-family: Rainy Hearts, sans-serif;
    font-size: 1.5em;
  }

  & .serverMessage {
    color: #b8860b;
  }

  & ul {
    z-index: 101;
    scroll-behavior: smooth;
    max-height: 150px;
    margin: 5px;
    padding: 3px;
    overflow-y: auto;
  }

  & li {
    pointer-events: none;
    font-size: 1.5em;
    list-style-type: none;
  }
}

@media (width >= 1px) {
  #chatBox {
    position: absolute;
  }
}
